<template>
    <div class="specinfo">
        <span v-html="sInfo.contents"></span>
    </div>
</template>

<script>
import Config      from "./../../../js/config.js"

export default {
    props: [],
    data() {
        return {
            sInfo: {},
            specInfo : "",
            comp_no : 0,
        };
    },
    mounted: function() {
        this.getUserSpecInfo();
    },
    methods: {
        getUserSpecInfo: function() {
            var vm = this;
            // vm.specInfo = "<p> </p><p><span style=\"color: rgb(230, 0, 0);\">2018.09.17  19:25   :      eth15   </span></p><p><span style=\"color: rgb(230, 0, 0);\">2018.09.12  01:05   : IDC      </span></p>"
            // this.specInfo = "";
            vm.comp_no = this.$store.state.user.comp_no;

            axios.get(Config.base_url+'/user/getuserspecinfo', {
                params: {
                    "bbs_id" : 2,
                    "comp_no": vm.comp_no,
                    "notice": true,
                }
            }).then(function(response) {
                // console.log(response);
                if(response.data.success == false){
                    // alert("  ");
                }else {
                    vm.sInfo = response.data.specinfo ;
                }
            });

        }
    }
}
</script>

<style scoped>
.specinfo {
    border-style: double;
}
</style>



